import React from 'react';
import VideoPlayer from './VideoPlayer.js';
import { TextSpot, LinkSpot, ButtonSpot, DecisionSpot } from './HotSpot.js';
import './App.css';
import '../node_modules/video.js/dist/video-js.css';

class App extends React.Component {
  constructor(props) {
    super(props);

    this.pause = this.pause.bind(this);
    this.rewind = this.rewind.bind(this);
    this.spongebob = this.spongebob.bind(this);
    this.nyancat = this.nyancat.bind(this);
    this.ppt = 'https://access-all-areas.s3.eu-west-2.amazonaws.com/JB00986US+Optison+Case+Studies+Series+II+Digital+Collateral+final+040621.pptx';
    this.pdf = 'https://access-all-areas.s3.eu-west-2.amazonaws.com/JB78592US+Optison+Albumin+EVA+-+Digital+-+FINAL+071020.pdf'
    this.state = {
      heading: 'Drone Alone',
      hotSpots: [],
      currentTime: 0
    };
  }

  componentDidMount() {
    this.player = document.getElementById("MainVideo").player;
    this.player.app = this;
    this.player.on('timeupdate', function (e) {
      this.app.updateVideoTime();
    });
  }

  componentWillUnmount() {
  }

  updateVideoTime() {
    let currentTime = this.player.currentTime();
    this.setState((state, props) => ({
      hotSpots: state.hotSpots,
      currentTime: currentTime
    }));
  }

  pause() {
    this.player.pause();
  }

  rewind() {
    this.player.currentTime(this.player.currentTime() - 3);
  }

  spongebob() {
    this.setState((state, props) => ({
      heading: 'Sponge Bob'
    }))
  }

  nyancat() {
    this.setState((state, props) => ({
      heading: 'Nyan Cat'
    }))
  }

  render() {
    const videoJsOptions = {
      autoplay: true,
      controls: true,
      sources: [{
        src: '/drone-shot.mp4',
        type: 'Video/mp4'
      }]
    };

    return (
      <div className="App">
        <h1>{ this.state.heading }</h1>
        <section className="Stage">
          <VideoPlayer id="MainVideo" { ...videoJsOptions }/>

          <TextSpot id="ts1"
                    className="TextSpot"
                    currentTime={ this.state.currentTime }
                    startsAt={ 1 }
                    endsAt={ 5 }
                    title="Hey There!"
                    body="This is floating text" />

          <LinkSpot id="ls"
                    className="Spot LinkSpot1"
                    currentTime={ this.state.currentTime }
                    startsAt={ 2 }
                    endsAt={ 8 }
                    linkHref={ this.ppt }
                    linkText="PPT File"
                    pause= { this.pause } />

          <LinkSpot id="ls"
                    className="Spot LinkSpot2"
                    currentTime={ this.state.currentTime }
                    startsAt={ 3 }
                    endsAt={ 8 }
                    linkHref={ this.pdf }
                    linkText="Adobe File"
                    pause= { this.pause } />

          <TextSpot id="ts2"
                    className="TextSpot"
                    currentTime={ this.state.currentTime }
                    startsAt={ 7 }
                    endsAt={ 8 }
                    title="Get ready..."
                    body="...here" />

          <TextSpot id="ts2"
                    className="TextSpot"
                    currentTime={ this.state.currentTime }
                    startsAt={ 8 }
                    endsAt={ 9 }
                    title="Get ready..."
                    body="...we" />

          <TextSpot id="ts2"
                    className="TextSpot"
                    currentTime={ this.state.currentTime }
                    startsAt={ 10 }
                    endsAt={ 11 }
                    title="Get ready..."
                    body="...Go." />

          <ButtonSpot id="bs1"
                      className="Spot ButtonSpot"
                      currentTime={ this.state.currentTime }
                      startsAt={ 12 }
                      endsAt={ 17 }
                      linkText="Back a bit"
                      payload= { this.rewind } />

          <LinkSpot id="ls"
                    className="Spot LinkSpot3"
                    currentTime={ this.state.currentTime }
                    startsAt={ 17 }
                    endsAt={ 23 }
                    linkHref="http://www.example.org"
                    linkText="External link"
                    pause= { this.pause } />

          <DecisionSpot id="ds"
                    className="Spot DecisionSpot"
                    currentTime={ this.state.currentTime }
                    startsAt={ 25 }
                    endsAt={ 99 }
                    decisionOne= { this.spongebob }
                    decisionTwo= { this.nyancat }
          />

        </section>
        <pre id="Time"> {this.state.currentTime} </pre>
      </div>
    );
  }
}

export default App;
